import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

const router = new Router({
    mode: 'history',
    base: process.env.BASE_URL,
    routes: [{
            path: '/',
            name: 'index',
            component: () =>
                import ('./views/Index.vue')
        },
        {
            path: '/film',
            name: 'film',
            component: () =>
                import ('./views/Film.vue')
        },
        {
            path: '/express',
            name: 'express',
            component: () =>
                import ('./views/Express.vue')
        },
        {
            path: '/recipe',
            name: 'recipe',
            component: () =>
                import ('./views/Recipe.vue')
        },
        {
            path: '/weather',
            name: 'weather',
            component: () =>
                import ('./views/Weather.vue')
        },
        {
            path: '/composition',
            name: 'composition',
            component: () =>
                import ('./views/Composition.vue')
        },
        {
            path: '/airQuality',
            name: 'airQuality',
            component: () =>
                import ('./views/AirQuality.vue')
        },
        {
            path: '*',
            redirect: '/'
        }

    ]
})


export default router;